export const normalizePath = (path) => {
    if (!path)
        return "/";
    const withoutQuery = path.split("?")[0]?.split("#")[0] ?? "/";
    let next = withoutQuery;
    if (!next.startsWith("/")) {
        next = `/${next}`;
    }
    if (next !== "/" && next.endsWith("/")) {
        next = next.slice(0, -1);
    }
    return next;
};
export const getActiveHeaderPath = (menuLinks, currentPath) => {
    const normalizedCurrentPath = normalizePath(currentPath);
    let bestPath = "";
    let bestLength = -1;
    for (const link of menuLinks.filter((item) => item.inHeader)) {
        const normalizedBasePath = normalizePath(link.path);
        const matches = (normalizedBasePath === "/" && normalizedCurrentPath === "/") ||
            (normalizedBasePath !== "/" &&
                (normalizedCurrentPath === normalizedBasePath ||
                    normalizedCurrentPath.startsWith(`${normalizedBasePath}/`)));
        if (matches && normalizedBasePath.length > bestLength) {
            bestPath = link.path;
            bestLength = normalizedBasePath.length;
        }
    }
    return bestPath;
};
